import { getInvoice } from '@/lib/actions/invoices';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import { formatCurrency, formatDate } from '@/lib/utils';

export default async function InvoiceDetailPage({ 
  params 
}: { 
  params: Promise<{ id: string }> 
}) {
  const { id } = await params;
  const invoice = await getInvoice(id);

  return (
    <div className="max-w-4xl">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold">Invoice {invoice.invoice_number}</h1>
        <div className="flex gap-2">
          <Link href={`/invoices/${invoice.id}/edit`}>
            <Button variant="secondary">Edit</Button>
          </Link>
          <Button>Generate PDF</Button>
        </div>
      </div>

      <Card className="mb-6">
        <div className="grid grid-cols-2 gap-6">
          <div>
            <h3 className="font-semibold mb-2">Bill To:</h3>
            <p className="font-medium">{invoice.clients?.name}</p>
            {invoice.clients?.address_line1 && <p className="text-sm">{invoice.clients.address_line1}</p>}
            {invoice.clients?.address_line2 && <p className="text-sm">{invoice.clients.address_line2}</p>}
          </div>
          <div className="text-right">
            <p><span className="font-semibold">Issue Date:</span> {formatDate(invoice.issue_date)}</p>
            <p><span className="font-semibold">Due Date:</span> {formatDate(invoice.due_date)}</p>
            <p><span className="font-semibold">Terms:</span> {invoice.terms}</p>
            <p className="mt-2">
              <span className={`text-xs px-2 py-1 rounded ${
                invoice.status === 'ready' 
                  ? 'bg-green-100 text-green-800' 
                  : 'bg-yellow-100 text-yellow-800'
              }`}>
                {invoice.status}
              </span>
            </p>
          </div>
        </div>
      </Card>

      <Card>
        <h3 className="font-semibold mb-4">Line Items</h3>
        <table className="w-full">
          <thead>
            <tr className="border-b">
              <th className="text-left py-2">Description</th>
              <th className="text-right py-2">Qty</th>
              <th className="text-right py-2">Rate</th>
              <th className="text-right py-2">Amount</th>
            </tr>
          </thead>
          <tbody>
            {invoice.invoice_items?.map((item: any) => (
              <tr key={item.id} className="border-b">
                <td className="py-3">{item.description}</td>
                <td className="text-right">{item.qty}</td>
                <td className="text-right">{formatCurrency(item.rate)}</td>
                <td className="text-right font-semibold">{formatCurrency(item.amount)}</td>
              </tr>
            ))}
          </tbody>
          <tfoot>
            <tr className="font-semibold">
              <td colSpan={3} className="text-right py-3">Subtotal:</td>
              <td className="text-right py-3">{formatCurrency(invoice.subtotal)}</td>
            </tr>
            <tr className="font-semibold">
              <td colSpan={3} className="text-right py-2">Tax:</td>
              <td className="text-right py-2">{formatCurrency(invoice.tax)}</td>
            </tr>
            <tr className="font-bold text-lg">
              <td colSpan={3} className="text-right py-3">Total:</td>
              <td className="text-right py-3">{formatCurrency(invoice.total)}</td>
            </tr>
          </tfoot>
        </table>
      </Card>
    </div>
  );
}
